.main_block {
    position: sticky;
    display: grid;
    top: 0;
    left: 0;
    display: inline-block;
    height: 100vh;
    padding: 0 10px;
    margin-right: 40px;
    background-color: #fff;
}

.links_initial {
    display: grid;
    position: relative;
    grid-template-columns: 1fr 5fr;
    grid-gap: 1rem;
    border-radius: 5px;
    width: 100%;
    display: grid;
    font-size: 20px;
    margin: 20px 0;
    justify-items: start;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    font-weight: bold;
    transition: 1s;
}

.links_initial:hover {
    background-color: #F2ECFC;
}

.links_hover_block {
    position: absolute;
    left: 0;
    top: 0;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    height: 100%;
    background-color: #6139EC;
    transition: 1s;
    animation: colorChange 0.5s;
    width: 50px;
}

@keyframes colorChange {
    from {
        width: 0px;
    } to {
        width: 50px;
    }
}

.logout_block {
    display: grid;
    align-items: end;
}

.logout {
    display: grid;
    grid-template-columns: 30px 50px;
    align-items: center;
    position: absolute;
    left: 10px;
    bottom: 20px;
    cursor: pointer;
    transition: 0.5s;
}

.logout:hover {
    color: red;
}