.content_block {
    margin: 50px;
}

.items_block {
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: repeat(7, 1fr);
    background-color: #fff;
    margin-top: 20px;
}

.items_block div {
    width: 100%;
    text-align: center;
    padding: 40px 10px;
    align-items: start;
}

.addIcon {
    transition: 0.5s !important;
    cursor: pointer;

}

.addIcon:hover {
    transform: rotate(360deg);
    color: #6139EC;
}

.edit_button {
    background-color: blue;
    color: #fff;
    font-weight: bold;
    transition: 0.5s;
}

.edit_button:hover {
    background-color: darkblue;
}