.modal_portal {
    position: sticky;
    z-index: 3;
}
.main_block {
    display: grid;
    justify-items: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}

.content_block {
    background-color: #F0EDE5;
    padding: 10px;
    width: 30rem;
}

.title {
    font-size: 25px;
    text-align: center;
    font-weight: bold;
    margin-bottom: 20px;
}

.content_block div {
    grid-gap: 1rem;
}

.absolute_name_input {
    width: 100%;
}

.absolute_name_translations_block {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.absolute_name_checkbox_block {
    display: flex;
    justify-content: space-around;
    margin: 10px 0;
}

.add_absolute_value_block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 20px;
}