.content_block {
    display: grid;
    width: 100%;
    justify-self: center;
    align-items: center;
    padding: 20px 0;
    margin: 0 auto;
    grid-template-columns: 2fr 2fr;
}

input {
    border-radius: 3px;
    padding: 10px;
}

.addSpecificationButton {
    width: 50%;
    justify-self: center;
}