.modal {
    display: grid;
    width: 100vw;
    height: 100vh;
    justify-items: center;
    align-items: center;
}

.content_block {
    display: grid;
    grid-template-columns: 1fr;
    width: 30rem;
    background-color: #F0EDE5;
    padding: 50px;
}

.regions_block {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    align-items: center;
}

.regions_block div {
    width: 5rem;
    height: 5rem;
    margin: 10px;
    background-color: gray;
    display: grid;
    justify-items: center;
    align-items: center;
}

.button_block {
    display: flex;
    justify-content: space-around;
}

.button_block button {
    width: 12rem;
}