.modal {
    width: 100vw;
    height: 100vh;
} 

.main_block {
    display: grid;
    justify-items: center;
    align-items: center;
    height: 100%;
}

.form {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    position: relative;
    width: 25%;
    background-color: #F0EDE5;
    border-radius: 10px;
    padding: 50px;
}