.content_header_block {
    justify-items: center;
    display: grid;
    width: 100%;
    font-weight: bold;
    padding: 20px 0;
    padding-bottom: 50px;
    align-self: start;
    height: 3rem;
    color: #848C9C;
    background-color: #F0EDE5;
    position: sticky;
    text-align: center;
    left: 0;
    top: 0;
    z-index: 1;
}

.content_header_block div {
    padding: 10px;
}