.modal {
    display: grid;
    width: 100vw;
    height: 100vh;
    justify-items: center;
    align-items: center;
}


.main_block {
    display: grid;
    grid-template-columns: 1fr;
    position: relative;
    background-color: #F0EDE5;
    width: 30rem;
}

.images {
    display: grid;
    justify-self: center;
    align-self: center;
    width: 10rem;
    height: 10rem;
}

.form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    align-items: center;
    margin-top: 30px;
}

.form button {
    width: 10rem;
    height: 3rem;
}