.modal_portal {
    z-index: 200;
}

.modal {
    display: grid;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    background: transparent;
}

.content_block {
    position: relative;
    background-color: #F0EDE5;
    display: grid;
    justify-self: center;
    align-self: center;
    width: 100%;
    width: 23rem;
    height: 23rem;
    padding: 50px;
}

.form {
    display: grid;
    align-items: center;
}

.form div {
    display: grid;
    justify-items: start;
    align-items: center;
    width: 95%;
}

form label {
    font-size: 20px;
    color: #fff;
    display: block;
}

form input {
    width: 90%;
    border-radius: 5px;
    padding-left: 10px;

}

.save_changes_button {
    background-color: yellow;
    justify-self: center;
    width: 90%;
    padding: 10px 20px;
}

.error_message {
    color: red;
}