.main_block { 
    display: grid;
    margin: 0px 50px;
}

.specifications_block {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    justify-items: center;
    align-items: center;
    text-align: center;
    width: 100%;
    padding: 20px 0px;
    margin-top: 20px;
    border-radius: 10px;
    background-color: #fff;
}

.edit_button {
    display: block;
    cursor: pointer;
    color: blue;
}

.packman_loader {
    display: grid;
    justify-self: center;
    align-self: center;
    margin-top: -50px;
    background-color: #F0EDE5;
}