.modal_portal {
    position: sticky;
    z-index: 3;
}

.modal {
    position: relative;
    display: grid;
    justify-items: center;
    align-items: center;
    width: 30rem;
    background-color: #F0EDE5;
    margin: 0 auto;
}

.content_block {
    display: grid;
    justify-items: center;
    align-items: center;
    margin: 0 50px;
}

.content_block img {
    margin: 20px 0;
}

.change_image_block {
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: 100px 100px;
}

.absolute_name {
    display: grid;
    justify-items: center;
    font-size: 50px;
}

.translations_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
}

.translations_block p {
    font-size: 20px;
    font-weight: bold;
    margin: 10px;
}

.active_specifications_block, .deleted_specifications_block {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 50px;
}