.modal_portal {
    z-index: 3;
    position: sticky;
}

.main_block {
    width: 50vw;
    margin: 0 auto;
    height: 100vh;
    display: grid;
    justify-items: center;
    align-items: center;
}

.content_block {
    display: grid;
    position: relative;
    grid-template-columns: 1fr;
    background-color: #F0EDE5;
    width: 30rem;
    justify-self: center;
    align-self: center;
    border-radius: 10px;
    padding: 50px;

}

.title {
    margin-top: 1rem;
    font-size: 25px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
}

.inputs_block {
    display: grid;
    margin: 0 auto;
    justify-items: start;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    width: 300px;
    margin-bottom: 30px;
}

.inputs_block > div {
    width: 100%;
    margin: 5px 0;
}